.header {
  background-color: #fff;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  align-items: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.header__logo:hover {
  text-decoration: none;
}

.header__nav-list {
  text-wrap: nowrap;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  text-transform: uppercase;
  margin-left: 20px;
}

.header__nav-item:first-child {
  margin-left: 0;
  position: relative;
}

.header__nav-link {
  color: #060b35;
  text-align: center;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.header__nav-link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-left: 3px;
  padding-bottom: 40px;
}

.card__name {
  text-align: left;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__code {
  color: #616070;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-left: 2px;
  padding-top: 17px;
  display: flex;
}

.card__review-text {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-top: 23px;
  padding-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__name {
  color: #34568b;
}

.stars {
  align-items: center;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}
